.modalWr {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    background-color: rgba(0,0,0,.3);
    backdrop-filter: blur(3px);
}

.modalInner {
    position: relative;
    padding: 3rem;
    background-color: white;
    border-radius: 8px;
    max-width: 480px;
    min-width: 320px;
}

.closeBtn {
    position: absolute;
    right: 10px;
    top: 10px;
}